@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --dark-yellow: #f6be00;
  --dark-green: #2f855a;
  --light-green: #aedac4;
  --dark-red: #cf142b;
  --light-red: #ffcccb;
}
body {
  background: #222;
  font-family: Arial, sans-serif;
}

button {
  font-size: 1.3em;
  line-height: 1em;
  padding: 0.5em 1em;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  color: #333;
  font-size: 1.3em;
  font-style: bold;
  background: white;
  margin-top: 2vh;
}

.container {
  margin: auto;
  max-width: 1200px;
  padding: 2%;
}
.headline,
.timer {
  font-family: "Permanent Marker", cursive;
  font-size: 4rem;
  color: #f6be00;
  color: var(--dark-yellow);
  margin-bottom: 2rem;
}

.phrase {
  margin-bottom: 0.2rem;
  display: -webkit-flex;
  display: flex;
}
.num-input,
.text-input {
  font-size: 1.3em;
  height: 2em;
  padding: 0.5em;
  border: 1px solid black;
  border-radius: 0.4em;
  color: #333;

  font-style: bold;
  background: white;
}

.num-input {
  margin-right: 0.4vw;
  min-width: 3em;
  -webkit-flex-basis: 15%;
          flex-basis: 15%;
}

.text-input {
  -webkit-flex-basis: 85%;
          flex-basis: 85%;
}

.timer {
  margin-top: 6vh;
}

button.start,
button.stop {
  font-size: 1.5rem;
  line-height: 2em;
  width: 6em;
}
button.start {
  color: #aedac4;
  color: var(--light-green);
  background: #2f855a;
  background: var(--dark-green);
}

button.stop {
  color: #ffcccb;
  color: var(--light-red);
  background: #cf142b;
  background: var(--dark-red);
}

.phrase {
  position: relative;
}

button.delete {
  position: absolute;
  right: 1em;
  top: calc(50% - 0.5em);
  height: 1em;
  width: 1em;
  margin: 0;
  padding: 0;
  opacity: 0.8;
}

button.delete img {
  height: 1em;
  width: 1em;
  top: 0;
  left: 0;
}

